
import { defineComponent } from 'vue';

export default defineComponent({
    __name: "BaseBanner",
    created() {
        this.local_duration = this.$props.duration;
    },
    props: {
        message: {
            type: String,
            default: ""
        },
        duration: {
            type: Number,
            default: 3000
        },
        type: {
            type: String,
            default: "info"
        }
    },

    data() {
        return {
            isVisible: false,
            local_duration: 0,
        }
    },
    methods: {
        show() {
            this.local_duration = this.$props.duration;
            if (!this.isVisible) {
                this.isVisible = true;
                this.runTime();
            }
        },

        runTime() {
            setTimeout(() => {
                this.local_duration -= 1000;
                this[(this.local_duration > 0) ? 'runTime' : 'hide']();
            }, 1000);
        },

        hide() {
            this.local_duration = 0;
            this.isVisible = false;
        }
    }
});
